*,*:before,*:after{box-sizing:border-box;margin:0;padding:0}.table{width:100%;max-width:100%;margin-bottom:1rem}
.table th,.table td{padding:.75rem;vertical-align:top;border-top:1px solid #eceeef}
.table thead th{vertical-align:bottom;border-bottom:2px solid #eceeef}.table tbody+tbody{border-top:2px solid #eceeef}
.table .table{background-color:#fff}.table-sm th,.table-sm td{padding:.3rem}.table-bordered{border:1px solid #eceeef}
.table-bordered th,.table-bordered td{border:1px solid #222;padding:10px}.table-bordered thead th,.table-bordered thead td{border-bottom-width:2px}
.table-striped tbody tr:nth-of-type(odd){background-color:rgba(0,0,0,0.05)}.table-hover tbody tr:hover{background-color:rgba(0,0,0,0.075)}
.table-active,.table-active>th,.table-active>td{background-color:rgba(0,0,0,0.075)}
.table-hover .table-active:hover{background-color:rgba(0,0,0,0.075)}.table-hover .table-active:hover>td,.table-hover .table-active:hover>th{background-color:rgba(0,0,0,0.075)}
.table-success,.table-success>th,.table-success>td{background-color:#dff0d8}.table-hover .table-success:hover{background-color:#d0e9c6}
.table-hover .table-success:hover>td,.table-hover .table-success:hover>th{background-color:#d0e9c6}
.table-info,.table-info>th,.table-info>td{background-color:#d9edf7}.table-hover .table-info:hover{background-color:#c4e3f3}
.table-hover .table-info:hover>td,.table-hover .table-info:hover>th{background-color:#c4e3f3}
.table-warning,.table-warning>th,.table-warning>td{background-color:#fcf8e3}.table-hover .table-warning:hover{background-color:#faf2cc}
.table-hover .table-warning:hover>td,.table-hover .table-warning:hover>th{background-color:#faf2cc}
.table-danger,.table-danger>th,.table-danger>td{background-color:#f2dede}.table-hover .table-danger:hover{background-color:#ebcccc}
.table-hover .table-danger:hover>td,.table-hover .table-danger:hover>th{background-color:#ebcccc}
.thead-inverse th{color:#fff;background-color:#292b2c}.thead-default th{color:#464a4c;background-color:#eceeef}
.table-inverse{color:#fff;background-color:#292b2c}.table-inverse th,.table-inverse td,.table-inverse thead th{border-color:#fff}
.table-inverse.table-bordered{border:0}.table-responsive{display:block;width:100%;overflow-x:auto;-ms-overflow-style:-ms-autohiding-scrollbar}
.table-responsive.table-bordered{border:0}ul.tabs{margin:0;padding:0;list-style:none}
ul.tabs li{background:0;color:#222;display:inline-block;padding:10px 15px;cursor:pointer}
ul.tabs li.current{background:#fff;color:#222}.tab-content{display:none;background:#fff;padding:15px}
.tab-content.current{display:inherit}#toggles{width:60px;text-align:center}.ios-toggle,.ios-toggle:active{position:absolute;top:-5000px;height:0;width:0;opacity:0;border:0;outline:0}
.checkbox-label{display:block;position:relative;padding:10px;margin-bottom:20px;font-size:12px;line-height:16px;width:100%;height:36px;-webkit-border-radius:18px;-moz-border-radius:18px;border-radius:18px;background:#f8f8f8;cursor:pointer}
.checkbox-label:before{content:'';display:block;position:absolute;z-index:1;line-height:34px;text-indent:40px;height:36px;width:36px;-webkit-border-radius:100%;-moz-border-radius:100%;border-radius:100%;top:0;left:0;right:auto;background:white;-webkit-box-shadow:0 3px 3px rgba(0,0,0,0.2),0 0 0 2px #ddd;-moz-box-shadow:0 3px 3px rgba(0,0,0,0.2),0 0 0 2px #ddd;box-shadow:0 3px 3px rgba(0,0,0,0.2),0 0 0 2px #ddd}
.checkbox-label:after{content:attr(data-off);display:block;position:absolute;z-index:0;top:0;left:-300px;padding:10px;height:100%;width:300px;text-align:right;color:#bfbfbf;white-space:nowrap}
.ios-toggle:checked+.checkbox-label{-webkit-box-shadow:inset 0 0 0 20px #13bf11,0 0 0 2px #13bf11;-moz-box-shadow:inset 0 0 0 20px #13bf11,0 0 0 2px #13bf11;box-shadow:inset 0 0 0 20px #13bf11,0 0 0 2px #13bf11}
.ios-toggle:checked+.checkbox-label:before{left:calc(100% - 36px);-webkit-box-shadow:0 0 0 2px transparent,0 3px 3px rgba(0,0,0,0.3);-moz-box-shadow:0 0 0 2px transparent,0 3px 3px rgba(0,0,0,0.3);box-shadow:0 0 0 2px transparent,0 3px 3px rgba(0,0,0,0.3)}
.ios-toggle:checked+.checkbox-label:after{content:attr(data-on);left:60px;width:36px}
#checkbox1+.checkbox-label{-webkit-box-shadow:inset 0 0 0 0 #13bf11,0 0 0 2px #ddd;-moz-box-shadow:inset 0 0 0 0 #13bf11,0 0 0 2px #ddd;box-shadow:inset 0 0 0 0 #13bf11,0 0 0 2px #ddd}
#checkbox1:checked+.checkbox-label{-webkit-box-shadow:inset 0 0 0 18px #13bf11,0 0 0 2px #13bf11;-moz-box-shadow:inset 0 0 0 18px #13bf11,0 0 0 2px #13bf11;box-shadow:inset 0 0 0 18px #13bf11,0 0 0 2px #13bf11}
#checkbox1:checked+.checkbox-label:after{color:#13bf11}